.App {
  display: grid;
  grid-template-columns: 260px 1fr;
  padding: 0.5rem;
  grid-gap: 0.5rem;
  height: 100vh;
}

.App__menu {
  overflow-y: hidden;
  display: grid;
  grid-template-rows: auto 1fr;
  grid-gap: 0.5rem;
}

@media (max-width: 600px) {
  .App {
    grid-template-columns: auto;
    height: 100%;
  }
}
